.eltdf-product-categories-holder{

	@include laptop-landscape-large{
		.eltdf-product-category{
			.eltdf-product-category-content{
				top: 58%;
			}
		}
	}
	
    @include ipad-landscape{
        $standard_list_columns: ('five', 'six');

        @for $i from 0 to length($standard_list_columns) {
            &.eltdf-#{nth($standard_list_columns,$i+1)}-columns {
                $column_width: 100%/3;

                .eltdf-product-category{
                    width: $column_width;
                }
            }
        }
	    
	    .eltdf-product-category{
		    .eltdf-product-category-content{
			    top: 40%;
		    }
	    }
    }
	@include ipad-portrait{
		$standard_list_columns: ('three','four','five', 'six');

		@for $i from 0 to length($standard_list_columns) {
			&.eltdf-#{nth($standard_list_columns,$i+1)}-columns {
				$column_width: 100%/2;

				.eltdf-product-category{
					width: $column_width;
				}
			}
		}
		
		.eltdf-product-category{
			.eltdf-product-category-content{
				top: 49%;
			}
		}
	}

    @include phone-landscape{
        $standard_list_columns: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($standard_list_columns) {
            &.eltdf-#{nth($standard_list_columns,$i+1)}-columns {
                $column_width: 100%;

                .eltdf-product-category{
                    width: $column_width;
                }
            }
        }
    }

}