@media only screen and (max-width: $ipad-portrait) {

    .woocommerce-wishlist {

        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {
            padding: 17px 25px;
        }
    }
}

@media only screen and (max-width: $phone-landscape) {

    .woocommerce-wishlist {

        .eltdf-container-inner {
            width: 98%;
        }

        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {
            padding: 14px 20px;
            line-height: 2em;

            a {
                float: none;
                margin: 0 70% 15px 0;
            }
        }

        table.wishlist_table {

            thead tr th {
                padding: 14px 10px 14px 0;
                font-size: 14px;

                &.product-name {
                    position: relative;
                }

                &.product-thumbnail {
                    display: none;
                }
                
                
            }

            tbody {

                tr {

                    td {

                        &.product-thumbnail {
                            display: none;
                        }

                        &.product-name {

                            a {
                                font-size: 12px;
                                //padding-left: 5px;
                                //padding-right: 5px;
                            }
                        }
                        

                        &.product-add-to-cart {

                            a {
                                padding: 5px;
                                font-size: 12px;
                                color: #fff;
                                border: 0;

                                &:after {
                                    height: 0;
                                }

                                &:hover {
                                    -webkit-animation: none;
                                    -moz-animation: none;
                                    -o-animation: none;
                                    animation: none;
                                }
                            }
                        }

                        &.product-price,
                        .product-stock-status {
                            font-size: 12px;
                        }
                    }
                }
            }
        }
    }
}