/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - begin
   ========================================================================== */

@media only screen and (min-width: $ipad-landscape-plus-pixel) and (max-width: $laptop-landscape-large) {

	.eltdf-woo-single-page {
		.eltdf-single-product-content {
			.images {
				width: 65%;
			}
		}

		.eltdf-single-product-summary {
			width: 35%;
		}
	}
}

@media only screen and (min-width: $ipad-landscape-plus-pixel) and (max-width: $laptop-landscape) {
    
    .eltdf-woo-single-page {
        .woocommerce-tabs {
            
            ul.tabs {
                
                > li {
                    
                    a {
                        padding: 12px 7px;
                    }
                }
            }
        }
    }
}

@include ipad-landscape {
    
    .eltdf-woo-single-page {
        
        .eltdf-single-product-content {
            
            .images,
            .eltdf-single-product-summary {
                float: none;
                width: 100%;
                padding: 0;
            }
	        
	        .images {
		
		        &.woocommerce-product-gallery--with-images {
			
			        .woocommerce-product-gallery__image {
				
				        &:not(:first-child) {
					        width: 25%;
				        }
			        }
		        }
		
		        .woocommerce-product-gallery__trigger {
			        right: 10px;
		        }
	        }
            
            .eltdf-single-product-summary {
                margin: 53px 0 0;
            }
        }
        
        .woocommerce-tabs {
            
            ul.tabs {
                
                > li {
                    
                    a {
                        padding: 13px 10px;
                    }
                }
            }
	
	        .entry-content {
		        > p {
			        margin: 28px 0 20px;
			        text-align: left;
		        }
	        }
        }
    }
}

@include phone-landscape {
    
    .eltdf-woo-single-page {
	    
	
	    &.eltdf-woo-single-thumb-on-left-side {
		
		    .eltdf-single-product-content {
			
			    .images {
				
				    &.woocommerce-product-gallery--with-images {
					
					    .woocommerce-product-gallery__image {
						
						    &:not(:first-child) {
							    width: 50%;
						    }
					    }
				    }
			    }
		    }
	    }
	
	    .eltdf-single-product-content {
		
		    .images {
			
			    &.woocommerce-product-gallery--with-images {
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: 50%;
					    }
				    }
			    }
		    }
	    }
	
	    .woocommerce-tabs {
		
		    ul.tabs {
			
			    > li {
				    width: 100%;
				    margin: 1px 4px;
			    }
		    }
	    }
    }
}

@include phone-portrait {
	.eltdf-woo-single-page {
		.woocommerce-tabs {

			ul.tabs {

				> li {
					margin: 1px 0;
				}
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - end
   ========================================================================== */