/* ==========================================================================
   WooCommerce Checkout page responsive styles - begin
   ========================================================================== */
@include ipad-landscape {

    .eltdf-woocommerce-page .woocommerce-checkout {

        .col-1,
        .col-2 {
            h3 {
                font-size: 29px;
            }
        }

        h3#order_review_heading {
            font-size: 29px;
        }
    }
}

@include ipad-portrait {
    
    .eltdf-woocommerce-page .woocommerce-checkout {
        
        .col-1,
        .col-2 {
            width: 100%;
        }
        
        .col-1 {
            margin: 0 0 25px;
        }
    }
}

@include phone-landscape {
    
    .eltdf-woocommerce-page .woocommerce-checkout {
        
        .col-1,
        .col-2 {
            
            .form-row {
                width: 100%;
                margin: 0;
                float: none;
            }
        }
    }
}

@include phone-portrait {
    
    .eltdf-woocommerce-page .woocommerce-checkout {
        
        .woocommerce-checkout-payment ul li.payment_method_paypal label img {
            display: none;
        }
    }
}
/* ==========================================================================
   WooCommerce Checkout page responsive styles - end
   ========================================================================== */