// layout mixins - start

@mixin eltdfRelativeHolderLayout($vertical-align: middle) {
    position: relative;
    display: inline-block;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin eltdfAbsoluteHolderLayout() {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

@mixin eltdfFlexBoxLayout($position: null, $align-items: null, $justify-content: null) {
    @if ($position) {
        position: $position;
    }
    
    @include eltdfFlexLayout();
    
    @if ($align-items) {
        @include eltdfFlexAlignItems($align-items);
    }
    
    @if ($justify-content) {
        @include eltdfFlexJustifyContent($justify-content);
    }
}

@mixin eltdfFlexContainer($align-items: null, $justify-content: null, $flex-direction: null, $flex-wrap: null, $align-content: null) {
	@include eltdfFlexBoxLayout(null, $align-items, $justify-content);
	
	@if ($flex-direction) {
		flex-direction: $flex-direction;
	}
	
	@if ($flex-wrap) {
		flex-wrap: $flex-wrap;
	}
	
	@if ($align-content) {
		align-content: $align-content;
	}
}

@mixin eltdfFlexLayout() {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
}

@mixin eltdfInlineFlexLayout() {
    display: -webkit-inline-flex;
    display: -ms-inline-flexbox;
    display: inline-flex;
}

@mixin eltdfFlexItem($order: 0, $flex-grow: 0, $flex-shrink: 1, $flex-basis: auto) {
    order: $order;
    flex-grow: $flex-grow;
    flex-shrink: $flex-shrink;
    flex-basis: $flex-basis;
}

@mixin eltdfFlexAlignItems($align-items) {
    $older-align-items: $align-items;
    
    @if ($align-items == 'flex-start') {
        $older-align-items: start;
    } @else if ($align-items == 'flex-end') {
        $older-align-items: end;
    }
    
    -webkit-box-align: $older-align-items;
    -webkit-align-items: $align-items;
    -ms-flex-align: $older-align-items;
    align-items: $align-items;
}

@mixin eltdfFlexJustifyContent($justify-content) {
    $older-justify-content: $justify-content;
    
    @if ($justify-content == 'flex-start') {
        $older-justify-content: start;
    } @else if ($justify-content == 'flex-end') {
        $older-justify-content: end;
    } @else if ($justify-content == 'space-between') {
        $older-justify-content: justify;
    }
    
    -webkit-box-pack: $older-justify-content;
    -webkit-justify-content: $justify-content;
    -ms-flex-pack: $older-justify-content;
    justify-content: $justify-content;
}

@mixin eltdfTableLayout() {
    position: relative;
    display: table;
    table-layout: fixed;
    height: 100%;
    width: 100%;
}

@mixin eltdfTableCellLayout($vertical-align: middle) {
    position: relative;
    display: table-cell;
    height: 100%;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin eltdfTypographyLayout($important: null) {
    color: inherit $important;
    font-family: inherit $important;
    font-size: inherit $important;
    font-weight: inherit $important;
    font-style: inherit $important;
    line-height: inherit $important;
    letter-spacing: inherit $important;
    text-transform: inherit $important;
}

@mixin eltdfBlockquote($important : null) {
    @include eltdfRelativeHolderLayout();
    margin: 10px 0 $important;
    padding: 20px 40px $important;
    font-size: 18px $important;
    line-height: 30px $important;
    text-align: center;
    quotes: none;
    box-sizing: border-box;
    border: none $important;
    color: $default-text-color $important;
    
    > * {
        @include eltdfTypographyLayout();
        margin: 0;
    }
    
    &:before,
    &:after {
        content: '';
    }
    
    cite,
    .wp-block-quote__citation,
    .wp-block-pullquote__citation,
    footer {
        display: block $important;
        margin-top: 10px $important;
        text-align: inherit $important;
        font-size: 14px $important;
        line-height: 1.3em $important;
        letter-spacing: 0 $important;
        font-style: normal  $important;
        font-weight: 400 $important;
        text-transform: none $important;
    }
    
}

// layout mixins - end

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// transition mixins - start

@mixin eltdfTransition($transition-param...) {
    -webkit-transition: $transition-param;
    -moz-transition: $transition-param;
    transition: $transition-param;
}

@mixin eltdfTransitionTransform($transition-param...) {
    -webkit-transition: -webkit-transform $transition-param;
    -moz-transition: -moz-transform $transition-param;
    transition: transform $transition-param;
}

@mixin eltdfTransform($transform-param...) {
    -webkit-transform: $transform-param;
    -moz-transform: $transform-param;
    transform: $transform-param;
}

@mixin eltdfAnimation($animation-param...) {
    -webkit-animation: $animation-param;
    -moz-animation: $animation-param;
    animation: $animation-param;
}

@mixin eltdfTransformOrigin($animation-param...) {
    -webkit-transform-origin: $animation-param;
    -moz-transform-origin: $animation-param;
    transform-origin: $animation-param;
}

// transition mixins - end

