// common mixins - start

@mixin eltdfBckImageStyle() {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin eltdfImageOverlayHoverStyle($with-hover: true) {
    
    @if ($with-hover) {
        
        &:hover {
            
            &:after {
                opacity: 1;
            }
        }

        &:after {
            @include eltdfAbsoluteHolderLayout();
            content: '';
            background-color: rgba(4,10,31,.75);
            opacity: 0;
            @include eltdfTransition(opacity 1.6s $default-easing);
        }

    } @else {
        @include eltdfAbsoluteHolderLayout();
        content: '';
        background-color: rgba(4,10,31,.75);
        opacity: 0;
        @include eltdfTransition(opacity .2s ease-in-out);
    }
}

@mixin eltdfStandardPaginationStyle($list_type: null) {
    @include eltdfRelativeHolderLayout(top);
    margin: 0;
    clear: both;

    ul {
        @include eltdfRelativeHolderLayout(top);
        padding: 0;
        margin: 0;
        list-style: none;
        text-align: center;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0 14px;

            a {
                position: relative;
                display: inline-block;
                vertical-align: top;
                color: $default-text-color;
                margin: 0;
                padding: 0;
            }

            &.eltdf-pag-active {
                
                a {
                    color: #121212;
                }
            }

            &.eltdf-pag-prev,
            &.eltdf-pag-next,
            &.eltdf-pag-first,
            &.eltdf-pag-last {
                margin: 0 7px;

                a {
                    font-size: 20px;

                    span {
                        display: block;
                        line-height: inherit;

                        &:before {
                            display: block;
                            line-height: inherit;
                        }
                    }
                }
            }

            @if ($list_type == 'shortcode') {
                
                &.eltdf-pag-prev {
                    
                    a {
                        opacity: 0;
                    }
                }

                &.eltdf-pag-next {
                    
                    a {
                        opacity: 1;
                    }
                }

            } @else if ($list_type == 'shop') {
                span {
                    position: relative;
                    display: inline-block;
                    vertical-align: top;
                    margin: 0;
                    padding: 0;
                    color: $default-heading-color;
                }

                a {
                    
                    &.next,
                    &.prev {
                        font-size: 0;
                        line-height: 0;

                        &:before {
                            display: block;
                            font-family: "linea-arrows-10";
                            font-size: 20px;
                            line-height: 25px;
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                        }
                    }

                    &.prev {
                        margin-right: -9px;

                        &:before {
                            position: relative;
                            top: 2px;
                            content: "\e03f";
                        }
                    }

                    &.next {
                        margin-left: -9px;

                        &:before {
                            position: relative;
                            top: 2px;
                            content: "\e04b";
                        }
                    }
                }
            }
        }
    }
}

@mixin eltdfButtonDefaultStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0;
    font-family: inherit;
    font-size: 12px;
    line-height: 2em;
    letter-spacing: 0.12em;
    font-weight: 400;
    text-transform: uppercase;
    outline: none;
    box-sizing: border-box;
    @include eltdfTransition(.4s $aux-easing);
}

@mixin eltdfButtonSize($size: medium) {
    
    @if ($size == 'small') {
        font-size: 10px;
        padding: 10px 19px;

    } @else if ($size == 'medium') {
        padding: 17px 40px;

    } @else if ($size == 'large') {
        padding: 17px 66px;

    } @else if ($size == 'huge') {
        display: block;
        width: 100%;
        text-align: center;
        padding: 17px 26px;
    }
}

@mixin eltdfButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
}

@mixin eltdfButtonSolidColor($important: null) {
    color: #fff $important;
    background-color: $default-button-color $important;
    border: 1px solid transparent $important;
}

@mixin eltdfButtonSolidHoverColor($important: null) {
    color: #fff $important;
    background-color: $default-button-hover $important;
}

@mixin eltdfButtonOutlineColor($important: null) {
    color: $default-heading-color $important;
    background-color: transparent $important;
    border: 1px solid $default-button-color $important;
}

@mixin eltdfButtonOutlineHoverColor($important: null) {
    border-color: $default-button-color $important;
}

@mixin eltdfPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }

    &:-moz-placeholder {
        @content;

        opacity: 1;
    }
    &::-moz-placeholder {
        @content;

        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content;
    }
}

// common mixins - end

