// mixins styles - start

%input-style {
    position: relative;
    width: 100%;
    margin: 0 0 $input-margin;
    padding: $input-vertical-padding $input-horizontal-padding;
    font-family: inherit;
    font-size: 14px;
    font-weight: inherit;
    line-height: 1.3em;
    color: $default-heading-color;
    background-color: transparent;
    border: 1px solid #dac4ab;
    border-radius: 0;
    outline: 0;
    cursor: text;
    -webkit-appearance: none;
    box-sizing: border-box;
    @include eltdfTransition(border-color 0.2s ease-in-out);

    &:focus {
        color: $default-heading-color;
        border-color: $default-text-color;
    }

    @include eltdfPlaceholder {
        color: inherit;
    }
}

@mixin select2-input-dropdown {
    .select2-container--default {
        .select2-selection--single {
            height: 60px;
            font-size: 14px;
            line-height: 1.3em;
            padding: 20px 16px;
            border-color: #dac4ab;
            color: $default-heading-color;

            .select2-selection__arrow {
                line-height: 60px;
                right: -4px;
            }
        }

        &.select2-container--open .select2-selection--single {
            border-color: $default-border-color;
        }
    }
}

// mixins styles - end

