// heading mixins - start

@mixin eltdfDefaultHeadingStyle() {
    @include eltdfHeadingStyle(0, false);
    margin: 25px 0;

    -ms-word-wrap: break-word;
    word-wrap: break-word;
    
    a {
        @include eltdfTypographyLayout();
        
        &:hover {
            color: $first-main-color;
        }
    }
}

@mixin eltdfHeadingStyle($with-heading: null, $with-color: true) {
    
    @if ($with-color) {
        color: $default-heading-color;
    }
    
    @if ($with-heading == 'h1') {
        @include eltdfH1();
    } @else if ($with-heading == 'h2') {
        @include eltdfH2();
    } @else if ($with-heading == 'h3') {
        @include eltdfH3();
    } @else if ($with-heading == 'h4') {
        @include eltdfH4();
    } @else if ($with-heading == 'h5') {
        @include eltdfH5();
    } @else if ($with-heading == 'h6') {
        @include eltdfH6();
    }
}

@mixin eltdfBody() {
    font-family: $default-text-font;
    font-size: 14px;
    font-weight: 400;
    line-height: 26px;
    color: $default-text-color;
    background-color: $default-background-color;
    -webkit-font-smoothing: antialiased;
}

@mixin eltdfH1() {
    font-size: 50px;
    line-height: 1.3em;
}

@mixin eltdfH2() {
    font-size: 30px;
    line-height: 1.3em;
}

@mixin eltdfH3() {
    font-size: 24px;
    line-height: 1.3em;
}

@mixin eltdfH4() {
    font-size: 18px;
    line-height: 1.3em;
}

@mixin eltdfH5() {
    font-size: 16px;
    line-height: 1.3em;
}

@mixin eltdfH6() {
    font-size: 14px;
    line-height: 1.3em;
}

// heading mixins - end

