/* ==========================================================================
   WooCommerce Quantity Inputs responsive styles - begin
   ========================================================================== */

@include ipad-portrait {

    .woocommerce-page .eltdf-content,
    div.woocommerce {

        .eltdf-quantity-buttons {

            .eltdf-quantity-input,
            .eltdf-quantity-minus,
            .eltdf-quantity-plus {
                font-size: 13px;
            }

            .eltdf-quantity-input {
                height: 58px;
                width: 58px;
                margin: 0 29px 0 0;
                line-height: 56px;
            }

            .eltdf-quantity-minus,
            .eltdf-quantity-plus {
                width: 29px;
                height: 29px;
                line-height: 28px;
            }
        }
    }
}

@include phone-portrait {
    
    .woocommerce-page .eltdf-content,
    div.woocommerce {
        
        .eltdf-quantity-buttons {
            
            .eltdf-quantity-input,
            .eltdf-quantity-minus,
            .eltdf-quantity-plus {
                font-size: 13px;
            }
            
            .eltdf-quantity-input {
                height: 50px;
                width: 50px;
                margin: 0 29px 0 0;
                line-height: 50px;
            }
            
            .eltdf-quantity-minus,
            .eltdf-quantity-plus {
                width: 29px;
                height: 25px;
                line-height: 25px;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs responsive styles - end
   ========================================================================== */